import { useEffect, useState } from "react"
import { useParams } from "react-router-dom";
import noImg from "../img/noImg.png";
import styles from "./Detail.module.css";
import YouTube from 'react-youtube';

import NavBar from "../components/NavBar";

function Detail() {


  const { id } = useParams();
  const [anime, setAnime] = useState([]);
  const [loading, setLoading] = useState(true);
  const getAnime = async () => {
    const json = await (await fetch(
      `https://kitsu.io/api/edge/anime/${id}`
    )).json();

    setAnime(json.data.attributes);
    setLoading(false);
    console.log(json);
  }

  useEffect(() => {
    getAnime();
  }, []);

  console.log(anime.canonicalTitle)

  return (
    <div>
      <NavBar />
      {
        loading ? <div></div> :
          <div className={styles.coverImg}>
            {
              anime.coverImage === null ? <img className={styles.noImg} src={noImg} alt=" " />
                : <img className={styles.Img} src={anime.coverImage.original} alt=" " />
            }
          </div>
      }
      <div className={styles.center}>
        <div className={styles.youtube}>
          <div className={styles.youtubeBox}>
            {
              anime.youtubeVideoId === null ? <img className={styles.noImg} src={noImg} alt=" " /> :
                <YouTube className={styles.Video}
                  //videoId : https://www.youtube.com/watch?v={videoId}      
                  videoId={anime.youtubeVideoId}
                  //opts():         .
                  //  .
                  opts={{
                    width: "800",
                    height: "450",
                    playerVars: {
                      autoplay: 0, // O
                      rel: 0, //    (   ..)
                      modestbranding: 1, //   youtube   
                    },
                  }}
                  //  
                  onEnd={(e) => { e.target.stopVideo(0); }}
                />
            }
          </div>
        </div>
      </div>
    </div>
  )
}

export default Detail;